import { h } from '@externs/preact'
import { Component } from '@externs/preact'

class Adonais extends Component {
  constructor() {
    super()
    this.state = { hint: null }
    // this.state = { hint: 'test', x: 10, y:10 }
  }
  getChildContext() {
    return {
      hint: this.state.hint,
      setHint: (hint) => {
        this.setState({ hint })
      },
      setCoordinates: ({ x, y }) => {
        this.setState({ x, y })
      },
    }
  }
  serverRender({ splendid, class: className }) {
    splendid.export()
    const cl = splendid.addCSS('styles/Adonais.css', 'Adonais')
    return (h('div',{'className':`Adonais ${className}`},
      h('img',{'className':cl`Image`,'alt':"Adonais: Art Deco, Documentation, Optimisation, NPM Packages, Automated Testing And Implementation",'src':"img/adonais/back.jpg",'style':"position:relative;"}),
    ))
  }
  render({ splendid, class: className }) {
    const cl = splendid.addCSS('styles/Adonais.css', 'Adonais')
    return (                               h('div',{'className':`Adonais ${className}`},
      h('img',{'className':cl`Image`,'src':"img/adonais/back.jpg",'style':"position:relative; opacity:0;"}),
      h('img',{'className':cl`Image`,'src':"img/adonais/back.jpg"}),
      this.state.hint && h('span',{'className':cl`Copy`,'style':{ 'z-index': 2, position: 'absolute', top: this.state.y + 15 , left: this.state.x + 15 }},
        h('span',{'className':cl`Noise`},this.state.hint),
      ),
      h(Letter,{top:112,file:"img/adonais/a2.png",left:219,class:cl`A Image`,href:"#art-deco"},
        h('strong',{'className':cl`Letter`},`A`),`rt Deco creates products for the modern web using back-end Node.JS and front-end JavaScript, taking advantage of the best available methods.`
      ),
      h(Letter,{top:208,file:"img/adonais/I.png",left:175,class:cl`I Image`,href:"#implementation"},
        `We `,h('strong',{'className':cl`Letter`},`I`),`mplement software products using the latest language features and spend time on writing code rather than setting up tools and installing 3rd party dependencies.`
      ),
      h(Letter,{top:207,file:"img/adonais/A3.png",left:73,class:cl`A2 Image`,href:"#automated-testing"},
        `Employed `,h('strong',{'className':cl`Letter`},`A`),`utomated Testing on unit and integration levels ensures that software is bug-free and that any future changes to the codebase won't break the existing version.`
      ),
      h(Letter,{top:123,file:"img/adonais/D.png",left:15,class:cl`D Image`,href:"#documentation"},
        `Full-coverage `,h('strong',{'className':cl`Letter`},`D`),`ocumentation allows for long-term maintenance by us and other teams and is full of examples and their outputs that also act as a quality assurance mechanism.`
      ),
      h(Letter,{top:28,file:"img/adonais/O.png",left:67,class:cl`O Image`,href:"#optimisation"},
        `The `,h('strong',{'className':cl`Letter`},`O`),`ptimisation of CSS stylesheets and JavaScript code, both for the server and client-side, means that transmitted bundles will load faster and improve user experience.`
      ),
      h(Letter,{top:25,file:"img/adonais/N.png",left:169,class:cl`N Image`,href:"#npm-packages"},
        `Having contributed over 150 `,h('strong',{'className':cl`Letter`},`N`),`PM packages, we have derived the methodology for managing dependencies that facilitate code reuse and organises workload into logical chunks.`
      ),
      h(Letter,{top:120,file:"img/adonais/S.png",left:125,class:cl`S Image`,href:"#software"},
        h('strong',{'className':cl`Letter`},`S`),`oftware Engineering is a craft that involves implementation, testing, documentation, building and maintenance. With our holistic approach, we are able to deliver best quality software.`
      ),
    )
    )
  }
}

const Letter = ({ file, top, left, children, class: className, href }, { setHint, setCoordinates, hint }) => {
  const style = { 'top': `${top}px`, 'left': `${left}px` }
  return (h('a',{'href':href},h('img',{'className':className,'src':file,'style':style,'onMouseOver':() => {
    setHint(children)
  },'onMouseOut':() => {
    setHint(null)
  },'onMouseMove':(e) => {
    if (!hint) return
    const img = /** @type {HTMLImageElement} */ (e.currentTarget)
    const state = {
      x: parseInt(img.style.left, 10) + e.offsetX,
      y: parseInt(img.style.top, 10) + e.offsetY,
    }
    setCoordinates(state)
  }})))
}

export default Adonais